.goods-list {
	float: left;
	width: 82%;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	padding: 0 1%;
	position: relative;
	padding-top: 30px;
	
	&--vertical {
		flex-direction: column;
		align-items: center;
	}
	
	&__btn {
		position: absolute;
		right: 0;
		top: 0;
		color: $dark-gray;
		visibility: hidden;
		
		&:hover {
			text-decoration: none;
		}
		&--visible {
			visibility: visible;
		}
	}
}
.good {
	position: relative;
	display: block;
	width: 19%;
	height: 260px;
	margin-bottom: 15px;
	border: 1px solid $border-color;
	text-decoration: none;
	
	&--vertical {
		width: 650px;
		height: 190px;
		@include clearfix;
		overflow: hidden;
	}
	
	&__image {
		padding: 10px;
		
		&--vertical {
			width: 35%;
			float: left;
		}
		
		img {
			display: block;
			width: 90%;
			margin: 0 auto;
		}
	}
	&__info {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		background-color: $gray;
		color: #fff;
		text-align: center;
		padding: 6px 0;
		
		&--vertical {
			position: static;
			width: 65%;
			float: right;
			background-color: transparent;
			color: $dark-gray;
			text-align: left;
			padding: 12px 10px 10px 0;
		}
	}
	&__title {
		margin: 0;
		text-align: center;
		padding: 0 10px 7px;
		font-size: 17px;
		
		&--vertical {
			display: inline-block;
			width: 326px;
			text-align: left;
			padding: 0;
		}
	}
	&__price {
		text-align: center;
		font-size: 16px;
		
		&--vertical {
			width: 80px;
			display: inline-block;
			text-align: right;
			font-weight: bold;
		}
	}
	&__description {
		visibility: hidden;
		position: absolute;
		left: -9999px;
		
		&--vertical {
			visibility: visible;
			position: static;
			left: 0;
			padding: 17px 0 24px;
			text-align: justify;
		}
	}
	&__buy {
		visibility: hidden;
		position: absolute;
		text-align: center;
		display: inline-block;
		padding: 8px 30px;
		background-color: $blue;
		color: #fff;
		text-shadow: 1px 1px 1px #273c4a;
		border-radius: 15px;
		font-weight: bold;
		
		&--vertical {
			visibility: visible;
			position: static;
			
			&:hover {
				background-color: darken($blue, 15%);
				transition: .4s;
			}
		}
	}
}

@media(max-width: 1380px) {
	.good {
		height: 220px;
	}
}
@media(max-width: 1300px) {
	.goods-list {
		width: 80%;
	}
	.good {
		width: 24%;
		height: 250px;
	}
}
@media(max-width: 1160px) {
	.good {
		width: 32%;
		height: 270px;
		
		&__title {
			font-size: 16px;
		}
		&__price {
			font-size: 15px;
		}
	}
}
@media(max-width: 1030px) {
	.goods-list {
		width: 77%;
	}
}
@media(max-width: 960px) {
	.goods-list {
		width: 74%;
	}
	.good {
		height: 240px;
	}
}
@media(max-width: 900px) {
	.good {
		height: 230px;
	}
}
@media(max-width: 800px) {
	.goods-list {
		width: 100%;
		float: none;
		padding: 0;
	}
	.good {
		height: 260px;
	}
}
@media(max-width: 700px) {
	.good {
		height: 235px;
	}
}
@media(max-width: 620px) {
	.good {
		width: 49%;
		height: 300px;
	}
}
@media(max-width: 550px) {
	.good {
		height: 275px;
	}
}
@media(max-width: 485px) {
	.good {
		height: 250px;
	}
}
@media(max-width: 440px) {
	.good {
		height: 230px;
	}
}
@media(max-width: 400px) {
	.good {
		width: 100%;
		height: 375px;
	}
}
@media(max-width: 360px) {
	.good {
		height: 345px;
